import { createServerClient } from "@/lib/supabase/server"

export interface UserCredits {
  credits: number
  total_earned: number
  total_spent: number
}

export interface CreditTransaction {
  id: string
  amount: number
  type: string
  description: string
  created_at: string
}

// 
export async function getUserCredits(userId: string): Promise<UserCredits | null> {
  const supabase = await createServerClient()

  const { data, error } = await supabase
    .from("user_credits")
    .select("credits, total_earned, total_spent")
    .eq("user_id", userId)
    .single()

  if (error) {
    console.error("Error fetching user credits:", error)
    return null
  }

  return data
}

// 
export async function spendCredits(
  userId: string,
  amount: number,
  type: string,
  description: string,
): Promise<boolean> {
  const supabase = await createServerClient()

  // 
  const credits = await getUserCredits(userId)
  if (!credits || credits.credits < amount) {
    return false
  }

  // 
  const { error: updateError } = await supabase
    .from("user_credits")
    .update({
      credits: credits.credits - amount,
      total_spent: credits.total_spent + amount,
      updated_at: new Date().toISOString(),
    })
    .eq("user_id", userId)

  if (updateError) {
    console.error("Error updating credits:", updateError)
    return false
  }

  // 
  await supabase.from("credit_transactions").insert({
    user_id: userId,
    amount: -amount,
    type,
    description,
  })

  return true
}

// 
export async function addCredits(
  userId: string,
  amount: number,
  type: string,
  description: string,
  referenceId?: string,
): Promise<boolean> {
  const supabase = await createServerClient()

  const credits = await getUserCredits(userId)
  if (!credits) return false

  // 
  const { error: updateError } = await supabase
    .from("user_credits")
    .update({
      credits: credits.credits + amount,
      total_earned: credits.total_earned + amount,
      updated_at: new Date().toISOString(),
    })
    .eq("user_id", userId)

  if (updateError) {
    console.error("Error updating credits:", updateError)
    return false
  }

  // 
  await supabase.from("credit_transactions").insert({
    user_id: userId,
    amount,
    type,
    description,
    reference_id: referenceId,
  })

  return true
}

// 
export async function getUserReferralCode(userId: string): Promise<string | null> {
  const supabase = await createServerClient()

  const { data, error } = await supabase.from("referral_codes").select("code").eq("user_id", userId).single()

  if (error) {
    console.error("Error fetching referral code:", error)
    return null
  }

  return data?.code || null
}

// 
export async function handleReferralSignup(newUserId: string, referralCode: string): Promise<boolean> {
  const supabase = await createServerClient()

  // 
  const { data: codeData, error: codeError } = await supabase
    .from("referral_codes")
    .select("user_id")
    .eq("code", referralCode)
    .single()

  if (codeError || !codeData) {
    return false
  }

  const referrerId = codeData.user_id

  // 
  const { error: referralError } = await supabase.from("referrals").insert({
    referrer_id: referrerId,
    referee_id: newUserId,
    referral_code: referralCode,
    reward_credits: 500,
    status: "completed",
    completed_at: new Date().toISOString(),
  })

  if (referralError) {
    console.error("Error creating referral:", referralError)
    return false
  }

  // 
  await addCredits(referrerId, 500, "referral", ``, newUserId)

  // 
  await addCredits(newUserId, 200, "referral", ``)

  // 
  await supabase
    .from("referral_codes")
    .update({ uses: supabase.rpc("increment", { x: 1 }) })
    .eq("user_id", referrerId)

  return true
}
